<template>
    <div class="contentManager">
        <leftNav :navTitle="navTitle" :chooseTitle="chooseTitle" @change-title="changeTitle"></leftNav>
        <userManager :chooseTitle="chooseTitle" v-show="chooseTitle=='会员管理'||chooseTitle=='游客管理'"></userManager>
        <publishManager :chooseTitle="chooseTitle" v-show="chooseTitle=='发布闲置'||chooseTitle=='发布寻求'||chooseTitle=='发布租赁'||chooseTitle=='发布赠送'"></publishManager>
        <storeManager :chooseTitle="chooseTitle" v-show="chooseTitle=='所有闲置'||chooseTitle=='所有寻求'||chooseTitle=='所有租赁'||chooseTitle=='所有赠送'"></storeManager>
        <announceManager :chooseTitle="chooseTitle" v-show="chooseTitle=='公告管理'"></announceManager>
        <feedBackManager :chooseTitle="chooseTitle" v-show="chooseTitle=='反馈管理'"></feedBackManager>
    </div>
</template>

<script>
import leftNav from './construct/leftNav.vue'
import userManager from './manager/userManager.vue'
import publishManager from './manager/publishManager.vue'
import storeManager from './manager/storeManager.vue'
import announceManager from './manager/announceManager.vue'
import feedBackManager from './manager/feedBackManager.vue'
export default {
    data(){
        return{
            chooseTitle:"会员管理",
            navTitle:[{
                content:"用户管理",
                isExtension:false,
                child:["会员管理","游客管理"]
            },{
                content:"发布管理",
                isExtension:false,
                child:["发布闲置","发布寻求","发布租赁","发布赠送"]
            },{
                content:"商品管理",
                isExtension:false,
                child:["所有闲置","所有寻求","所有租赁","所有赠送"]
            },{
                content:"公告管理",
                isExtension:false,
                child:""
            },{
                content:"反馈管理",
                isExtension:false,
                child:""
            },]
        }
    },
    components:{
        leftNav,
        userManager,
        publishManager,
        storeManager,
        announceManager,
        feedBackManager
    },
    methods:{
        changeTitle(title,type){
            if(type=="parent"){
                if(title.child.length==0){
                    this.chooseTitle=title.content;
                }else{
                    // 展开或关闭
                    title.isExtension=!title.isExtension;
                }
            }else if(type=="child"){
                this.chooseTitle=title
            }
        }
    }
}
</script>

<style lang="less" scoped>
.contentManager{
    display: flex;
}
</style>
