<template>
    <canvas ref="canvas"></canvas>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    data(){
        return{
            colors :["#8a94aa", "#7c8970", "#c8c0d5", "#fce9d6", "#935354", "#365090", "#676465"]
        }
    },
    props:["width","height"],
    computed:{
        ...mapGetters("analyse",{
            sectorData:"getSectorData"
        })
    },
    methods:{
        // 前面index个数相加
        preSum(index, arr) {
            let sum = 0;
            for (let i = 0; i < index + 1; i++)
                sum += arr[i];
            return sum;
        },
        // 角度转换
        degTurn(num, sum) {
            return Math.ceil(num * 2 / sum * 10) / 10;
        },
        // 绘制空心扇形图
        drawSector(c,x,y,l,arr,colors){
            let sum=0;
            arr.forEach((num)=>{
                sum+=num;
            })
            for (let index = 0; index < arr.length; index++) {
                let next = {
                        x: parseInt(x - l * Math.cos(this.degTurn(this.preSum(index, arr), sum) * Math.PI)),
                        y: parseInt(y - l * Math.sin(this.degTurn(this.preSum(index, arr), sum) * Math.PI))
                    }
                c.strokeStyle = "rgb(0,0,0)";
                c.fillStyle = colors[index];
                c.beginPath();
                c.moveTo(x, y);
                if (index == 0) {
                    c.lineTo(x - l, y);
                    c.arc(x, y, l, Math.PI, (1 + this.degTurn(this.preSum(index, arr), sum)) * Math.PI, false);
                } else {
                    let pre = {
                        x: x - l * Math.cos(this.degTurn(this.preSum(index - 1, arr), sum) * Math.PI),
                        y: y - l * Math.sin(this.degTurn(this.preSum(index - 1, arr), sum) * Math.PI)
                    }
                    c.lineTo(pre.x, pre.y);
                    c.arc(x, y, l, (1 + this.degTurn(this.preSum(index - 1, arr), sum)) * Math.PI, (1 + this.degTurn(this.preSum(index, arr), sum)) * Math.PI, false);
                }
                c.lineTo(x, y);
                c.closePath();
                c.fill();
            }
            c.moveTo(x,y);
            c.fillStyle="rgb(255,255,255)";
            c.beginPath();
            c.arc(x,y,l/2,0,2*Math.PI)
            c.closePath();
            c.fill();
        },
        tipDraw(c,text, colors) {
            let per = 32;
            let left = 140;
            let top = 347;
            let width = 20
            let height = 16;
            for (let index = 0; index < text.length; index++) {
                c.beginPath();
                c.textAlign = 'left';
                c.textBaseline = 'middle';
                c.font = '18px Adobe Ming Std';
                c.fillStyle = "rgb(153,153,153)";
                c.fillText(text[index], left+width+20, top+height-6 + per * index);
                c.closePath();
                this.rectDraw(c,left, top + per * index, left+width, top+height + per * index, colors[index]);
                if(index>=text.length/2-1){
                    left=315;
                    top=347-(parseInt(text.length/2)+1)*per;
                }
            }
        },
        rectDraw(c,x1, y1, x2, y2, color) {
            c.beginPath();
            c.moveTo(x1, y1);
            c.lineTo(x1, y2);
            c.lineTo(x2, y2);
            c.lineTo(x2, y1);
            c.strokeStyle = 'rgb(0,0,0)';
            c.fillStyle = color;
            c.fill();
            c.closePath();
        },
        draw(){
            let canvas=this.$refs.canvas;
            let context=canvas.getContext('2d');
            context.clearRect(0,0,this.width,this.height);
            let text=[];
            let num=[];
            this.sectorData.forEach((data,index)=>{
                text[index]=data.name;
                num[index]=data.num;
            })
            this.drawSector(context,265,200,100,num,this.colors);
            this.tipDraw(context,text,this.colors)
        }
    },
    mounted(){
        let canvas=this.$refs.canvas;
        canvas.width=this.width;
        canvas.height=this.height;
        this.draw();
    },
    watch:{
        sectorData:function(newVal,oldVal){
            console.log('oldVal: ', oldVal);
            console.log('newVal: ', newVal);
            this.draw();
        }
    }
}
</script>

<style scoped>
canvas{
    border: 4px solid #dbdbdb;
    transform: translateY(4px);
}
</style>
