<template>
    <div class="workBench">
            <leftNav @change-title="changeTitle" :chooseTitle="chooseTitle" :navTitle="navTitle"></leftNav>
            <Index v-show="chooseTitle=='后台首页'" :chooseTitle="chooseTitle"></Index>
            <System v-show="chooseTitle=='系统信息'" :chooseTitle="chooseTitle"></System>
            <Password v-show="chooseTitle=='修改密码'" :chooseTitle="chooseTitle"></Password>
    </div>
</template>

<script>
import leftNav from './construct/leftNav.vue'
import Index from './work/index.vue'
import System from './work/system.vue'
import Password from './work/password.vue'
export default {
    data(){
        return{
            chooseTitle:"后台首页",
            navTitle:[{
                content:"后台首页",
                isExtension:false,
                child:""
            },{
                content:"系统信息",
                isExtension:false,
                child:""
            },{
                content:"修改密码",
                isExtension:false,
                child:""
            },]
        }
    },
    components:{
        leftNav,
        Index,
        System,
        Password
    },
    methods:{
        changeTitle(title,type){
            if(type=="parent"){
                if(!title.isExtension){
                    this.chooseTitle=title.content;
                }else{
                    // 展开或关闭
                }
            }else if(type=="child"){
                this.chooseTitle=title
            }
        }
    }
}
</script>

<style lang="less" scoped>
@import "../assets/less/define.less";
.workBench{
    display: flex;
}
</style>
