<template>
    <div class="topNav">
        <div class="topNavCon">
            <span>后台管理</span>
            <span>> {{selectIndex | turnName}}</span>
            <span v-if="parentTitle!=''">> {{parentTitle}}</span>
            <span>> {{chooseTitle}}</span>
        </div>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    props:["chooseTitle","parentTitle"],
    computed:{
        ...mapGetters('user',{
            selectIndex:"getSelectIndex"
        })
    },
    filters:{
        turnName(val){
            let arr=['工作台','内容管理','访问统计']
            return arr[val];
        }
    }
}
</script>

<style lang="less">
@import "../../assets/less/define.less";
.topNav{
    display: inline-block;
    width: 1660px;
    margin: 10px;
    padding: 10px;
    background-color: #fff;
    .topNavCon{
        padding-left: 20px;
        height: 30px;
        border-left: 6px solid @topicDeepBColor;
        span{
            padding: 0px 5px;
            line-height: 30px;
            font-size: 18px;
            color:@chatDeepBColor;
        }
    }
}
</style>
