<template>
    <div class="transaction">
        <span class="title">交易总量</span>
        <div class="data">
            <div class="wholeData">
                <span>（最近7天）</span>
                <span class="bigNum">{{wholeData.sellAmount}}</span>
            </div>
            <div class="detailData">
                <div>
                    <div>闲置<span class="middleNum">{{wholeData.sellIdleNumber}}</span></div>
                    <div>寻求<span class="middleNum">{{wholeData.sellSeekNumber}}</span></div>
                </div>
                <div>
                    <div>租赁<span class="middleNum">{{wholeData.sellLeaseNumber}}</span></div>
                    <div>赠送<span class="middleNum">{{wholeData.sellGiveNumber}}</span></div>
                </div>
            </div>
            <canvas ref="canvas"></canvas>
        </div>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    data(){
        return {
            colors :["rgb(153,153,153)", "rgb(102,153,102)", "rgb(204,204,204)", "rgb(255,255,204)", "rgb(153,102,102)", "rgb(51,102,153)", "rgb(102,102,102)"],
            canvas:"",
            text:["书籍","文具","日用","美妆","食品","电器","其他"]
        }
    },
    computed:{
        ...mapGetters('work',{
            wholeData:"getWholeData"
        })
    },
    methods:{
        // 前面index个数相加
        preSum(index, arr) {
            let sum = 0;
            for (let i = 0; i < index + 1; i++)
                sum += arr[i];
            return sum;
        },
        // 角度转换
        degTurn(num, sum) {
            return Math.ceil(num * 2 / sum * 10) / 10;
        },
        // 绘制空心扇形图
        drawSector(c,x,y,l,arr,colors){
            let sum=0;
            arr.forEach((num)=>{
                sum+=num;
            })
            for (let index = 0; index < arr.length; index++) {
                // console.log(degTurn(preSum(index, arr), sum));
                let next = {
                        x: parseInt(x - l * Math.cos(this.degTurn(this.preSum(index, arr), sum) * Math.PI)),
                        y: parseInt(y - l * Math.sin(this.degTurn(this.preSum(index, arr), sum) * Math.PI))
                    }
                    // console.log("x:  " + next.x + "  y:  " + next.y);
                c.strokeStyle = "rgb(0,0,0)";
                c.fillStyle = colors[index];
                c.beginPath();
                c.moveTo(x, y);
                if (index == 0) {
                    c.lineTo(x - l, y);
                    c.arc(x, y, l, Math.PI, (1 + this.degTurn(this.preSum(index, arr), sum)) * Math.PI, false);
                } else {
                    let pre = {
                        x: x - l * Math.cos(this.degTurn(this.preSum(index - 1, arr), sum) * Math.PI),
                        y: y - l * Math.sin(this.degTurn(this.preSum(index - 1, arr), sum) * Math.PI)
                    }
                    c.lineTo(pre.x, pre.y);
                    c.arc(x, y, l, (1 + this.degTurn(this.preSum(index - 1, arr), sum)) * Math.PI, (1 + this.degTurn(this.preSum(index, arr), sum)) * Math.PI, false);
                }
                c.lineTo(x, y);
                c.closePath();
                c.fill();
            }
            c.moveTo(x,y);
            c.fillStyle="rgb(255,255,255)";
            c.beginPath();
            c.arc(x,y,l/2,0,2*Math.PI)
            c.closePath();
            c.fill();
        },
        tipDraw(c,text, colors) {
            let per = 28;
            for (let index = 0; index < text.length; index++) {
                c.beginPath();
                c.textAlign = 'left';
                c.textBaseline = 'middle';
                c.font = '18px Adobe Ming Std';
                c.fillStyle = "rgb(153,153,153)";
                c.fillText(text[index], 360, 39 + per * index);
                c.closePath();
                this.rectDraw(c,320, 30 + per * index, 340, 45 + per * index, colors[index]);
            }
        },
        rectDraw(c,x1, y1, x2, y2, color) {
            c.beginPath();
            c.moveTo(x1, y1);
            c.lineTo(x1, y2);
            c.lineTo(x2, y2);
            c.lineTo(x2, y1);
            c.strokeStyle = 'rgb(0,0,0)';
            c.fillStyle = color;
            c.fill();
            c.closePath();
        }
    },
    mounted(){
        this.canvas=this.$refs.canvas;
        this.canvas.width=400;
        this.canvas.height=260;
        let context = this.canvas.getContext('2d');
        this.drawSector(context,170,130,100,[4,5,1,6,7,8],this.colors);
        this.tipDraw(context,this.text,this.colors)
        context.beginPath();
        context.font = '18px Adobe Ming Std';
        context.fillStyle = "rgb(255,102,102)";
        context.fillText('交易统计',10,24);
    }
}
</script>

<style lang="less" scoped>
.transaction{
    display: flex;
    width: 1200px;
    height: 300px;
    margin: 10px;
    padding: 20px;
    box-sizing: border-box;
    font-size: 18px;
    background-color: #fff;
    color: #999;
    .title{
        color: #2c3242;
    }
    .data{
        display: flex;
        justify-content: space-between;
        width: 1000px;
        .wholeData{
            display: flex;
            flex-direction: column;
            .bigNum{
                margin-top: 62px;
                color: #99c;
            }
        }
        .detailData{
            margin-top: 54px;
            &>div{
                display: flex;
                justify-content: space-between;
                width: 400px;
                margin: 18px 0px 60px 0px;
                &>div{
                    width: 127px;
                }
                .middleNum{
                    margin-left: 20px;
                    font-size: 40px;
                    color: #99c;
                }
            }
        }
    }
}
</style>
