<template>
    <div class="header">
        <div class="top">
            <div class="topLeft">
                <img src="../../assets/img/logo.png">
                <span>蚂蚁置物</span>
            </div>
            <div class="topRight">
                <img class="userImg" :src="userInfo.img">
                <span class="userName">{{userInfo.name}}</span>|
                <span class="linkFront">前台首页</span>|
                <span class="back" @click="isOutTip=true">退出</span>
                <img src="../../assets/img/icon/back.png" @click="isOutTip=true">
            </div>
        </div>
        <div class="nav">
            <div :class="['navBtn',selectIndex==0?'chooseNav':'']" @click="changeIndex(0)">工作台</div>
            <div :class="['navBtn',selectIndex==1?'chooseNav':'']" @click="changeIndex(1)">内容管理</div>
            <div :class="['navBtn',selectIndex==2?'chooseNav':'']" @click="changeIndex(2)">访问统计</div>
        </div>
        <div class="outTip" v-if="isOutTip">
            <h1>你确定要退出登录状态吗</h1>
            <button @click="outOfLogin">确定</button>
            <button @click="isOutTip=false">取消</button>
        </div>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'

export default {
    data(){
        return{
            isOutTip:false
        }
    },
    computed:{
        ...mapGetters("user",{
            userInfo:"getUserInfo",
            selectIndex:"getSelectIndex"
        })
    },
    mounted(){
        console.log(this.userInfo);
    },
    methods:{
        ...mapActions('user',["changeIndex"]),
        outOfLogin(){
            this.$router.push('/login')
        }
    },
}
</script>

<style lang="less" scoped>
@import url("../../assets/less/define.less");
.header{
    color: #fff;
    .top{
        display: flex;
        height: 100px;
        justify-content: space-between;
        align-items: center;
        background-color: @topicShallowBColor;
        >div{
            display: flex;
            align-items: center;
        }
        .topLeft{
            margin-left: 58px;
            img{
                margin: 10px 20px;
            }
            span{
                font-size: 36px;
            }
        }
        .topRight{
            margin-right: 70px;
            font-size: 18px;
            .userImg{
                width: 60px;
                height: 60px;
                border-radius: 50%;
            }
            .userName,.linkFront{
                padding: 0px 20px;
            }
            .back{
                padding:0px 10px 0px 20px;
                cursor: pointer;
            }
            img{
                cursor: pointer;
            }
        }
    }
    .nav{
        background-color: @topicDeepBColor;
        .navBtn{
            display: inline-block;
            padding: 15px 67px;
            font-size: 20px;
            cursor: pointer;
        }
        .chooseNav{
            background-color: @topicChooseBColor;
        }
    }
    .outTip{
        position: absolute;
        top: 30%;
        left: 50%;
        width: 400px;
        border: 4px solid @topicShallowBColor;
        text-align: center;
        background-color: #fff;
        transform: translateX(-50%) translateY(-50%);
        h1{
            margin: 20px;
            border-bottom: 2px solid @topicChooseBColor;
            text-align: center;
            color: @topicShallowBColor;
        }
        button{
            padding: 10px 20px;
            margin: 20px 10px 10px 10px;
            border:1px solid @topicShallowBColor;
            font-size: 20px;
            color: @topicShallowBColor;
            background-color: #fff;
            &:hover{
                background-color: @topicShallowBColor;
                color: #fff;
                cursor: pointer;
            }
        }
    }
}
</style>
