import axios from 'axios'
const namespaced = true;
const state = {
    wholeData: {
        sellLeaseNumber: 100,
        othersNumber: 50,
        sellGiveNumber: 100,
        releaseLeaseNumber: 110,
        stationeryNumber: 50,
        allUsers: 500,
        releaseSeekNumber: 200,
        personalData: {
            currentVersion: "蚂蚁置物网站1.0",
            adminName: "admin",
            lastLoginTime: "2019-09-04 00:55:34",
            lastLoginIp: "127.0.0.1",
            loginTime: "2019-09-20 21:22:15",
            loginIp: "169.254.236.254"
        },
        releaseIdleNumber: 200,
        booksNumber: 100,
        dailyNumber: 50,
        sellSeekNumber: 100,
        userRegisterNumber: 50,
        sellIdleNumber: 200,
        releaseAmount: 500,
        sellAmount: 300,
        electricalNumber: 50,
        cosmeticsNumber: 60,
        foodNumber: 100,
        releaseGiveNumber: 50
    },
    systemData: {
        environmentalSpecificationName: "Java Platform API Specification",
        usedMemory: "",
        totalMemory: "",
        virtualMachineSpecificationName: "",
        userWorkingDirectory: "",
        userHomeDirectory: "",
        usersOfTheSystem: "",
        operatingSystemName: "",
        freeMemory: ""
    }
}
const getters = {
    getWholeData(state) {
        return state.wholeData
    },
    getSystemData(state) {
        return state.systemData
    }
}
const mutations = {
    setWholeData(state, data) {
        state.wholeData = data
    },
    setSystemData(state, data) {
        state.systemData = data;
    }
}
const actions = {
    requestWholeData({ commit }) {
        axios.get('/background/homePage/getHomePageData')
            .then(res => {
                commit('setWholeData', res.data);
            }).catch(err => {
                console.log(`the error happen because ${err}`)
            })
    },
    requestSystemData({ commit }) {
        axios.get("/background/homePage/getSystemMessage")
            .then(res => {
                commit('setSystemData', res.data);
            }).catch(err => {
                console.log(`the error happen when request SystemData because ${err}`)
            })
    }
}

export default { state, getters, mutations, actions, namespaced }