<template>
    <div class="leftNav">
        <ul>
            <li 
                v-for="title in navTitle" 
                :class="[chooseTitle==title.content&&title.child.length==0?'chooseTitle':'','navBtn']"
                @click="$emit('change-title',title,'parent')">{{title.content}}
                <span v-if="title.child.length>0" class="iconChange">{{title.isExtension?'-':'+'}}</span>
                <ul v-show="title.isExtension">
                    <li v-for="c in title.child"
                    @click.stop="$emit('change-title',c,'child')"
                    :class="[chooseTitle==c?'chooseTitle':'','childBtn']">
                        {{c}}
                    </li>
                </ul>
                </li>
        </ul>
    </div>
</template>

<script>
export default {
    data(){
        return{
        }
    },
    props:["chooseTitle","navTitle"]
}
</script>

<style lang="less">
@import "../../assets/less/define.less";
.leftNav{
    display: inline-block;
    width: 240px;
    height: 930px;
    background-color: @topicDeepBColor;
    color: #fff;
    .navBtn{
        position: relative;
        width: 240px;
        padding: 20px 80px 20px 67px;
        box-sizing: border-box;
        margin-bottom: 4px;
        font-size: 20px;
        background-color: @topicShallowBColor;
        cursor: pointer;
        ul{
            list-style: none;
            transform: translateY(20px);
            li{
                width: 100%;
                transform: translateX(-67px);
            }
        }
        .iconChange{
            position: absolute;
            left: 40px;
            top: 25px;
            width: 16px;
            height: 16px;
            box-sizing: border-box;
            border: 2px solid @topicChooseBColor;    
            font-size: 10px;
            text-align: center;
            line-height: 10px;
        }
    }
    .chooseTitle{
        &::after{
            content:"";
            display: inline-block;
            position: absolute;
            right: 27px;
            top: 24px;
            width: 0px;
            height: 0px;
            border: 10px solid #fff;
            border-color: transparent transparent transparent #fff;
        }
    }
    .childBtn{
        padding: 20px 80px 20px 67px;
        background-color: @topicChooseBColor;
    }
}
</style>
