<template>
    <div class="password">
        <topNav :chooseTitle="chooseTitle" :parentTitle="''"></topNav>
        <div class="mainInfoContainer">
            <div class="mainInfo">
                <p>
                    <span class="label">原密码：</span>
                    <input type="password" v-model="originalPassword" :class="this.oJudge?'errInput':''">
                </p>
                <p>
                    <span class="label">新密码：</span>
                    <input type="password" v-model="newPassword" :class="this.nJudge?'errInput':''">
                </p>
                <p>
                    <span class="label">确认密码：</span>
                    <input type="password" v-model="surePassword" :class="this.sJudge?'errInput':''">
                </p>
                <button @click="storePassword">保存</button>
            </div>
        </div>
    </div>
</template>

<script>
import topNav from '../construct/topNav.vue'
import axios from 'axios'
export default {
    data(){
        return{
            originalPassword:"",
            newPassword:"",
            surePassword:"",
            oJudge:false,
            nJudge:false,
            sJudge:false
        }
    },
    props:["chooseTitle"],
    components:{
        topNav
    },
    methods:{
        storePassword(){
            this.oJudge=false;
            this.nJudge=false;
            this.sJudge=false;
            if(this.originalPassword===""||this.newPassword===""||this.surePassword===""){
                this.oJudge=this.originalPassword==="";
                this.nJudge=this.newPassword==="";
                this.sJudge=this.surePassword==="";
            }else{
                if(this.newPassword!==this.surePassword){
                    alert("修改后的密码应和确认密码一致")
                }else{
                    axios.patch(`/background/administrator/update?oldPassword=${this.originalPassword}&newPassword=${this.newPassword}`)
                        .then(res=>{
                            alert(res.data.msg)
                            this.originalPassword="";
                            this.newPassword="";
                            this.surePassword="";
                        }).catch(err=>{
                            console.log(`the error called ${err} when change password`)
                        })
                }
            }
        }
    }
}
</script>

<style lang="less" scoped>
.password{
    .mainInfoContainer{
        position: relative;
        width: 1560px;
        height: 846px;
        margin-left: 10px;
        background-color: #fff;
        .mainInfo{
            position: absolute;
            left: 50%;
            padding-top:100px; 
            line-height: 40px;
            font-size: 18px;
            text-align: center;
            color: #333;
            transform: translateX(-50%);
            p{
                margin-bottom: 27px;
                .label{
                    display: inline-block;
                    width: 90px;
                    text-align: right;
                }
                input{
                    width: 300px;
                    height: 40px;
                    box-sizing: border-box;
                    border: 1px solid #666;
                    border-radius:5px;
                    font-size: 18px;
                    text-indent: 10px;
                    outline:none;
                }
            }
            .errInput{
                border: 2px solid #f00;
            }
            button{
                width: 80px;
                height: 40px;
                border: none;
                border-radius: 5px;
                font-size: 18px;
                outline: none;
                color: #fff;
                background-color: #369;
                cursor: pointer;
            }
        }
    }
}
</style>
