<template>
    <canvas ref="canvas"></canvas>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    name:"lineChart",
    data(){
        return{
            lineColor:["#f04f4f","#24b7e7","#7064b7"],
            lineTip:["学生身份","教师身份","游客身份"],
            axisNum:7,
            axisLimit:20
        }
    },
    computed:{
        ...mapGetters("analyse",{
            lineData:"getLineData"
        })
    },
    props:["chooseTitle","topLength"],
    methods:{
        ...mapActions("analyse",["requsetAnalyseData"]),
        // 绘制文字
        drawText(c,x,y,text,color,align='left',size="18px"){
            c.fillStyle=color;
            c.beginPath();
            c.textAlign = align;
            c.textBaseline = 'middle';
            c.font = `${size} Adobe Ming Std`;
            c.closePath();
            c.fillText(text, x,y);
        },
        // 绘制小圆圈
        drawCircle(c,x,y,l=3,color='#999'){
            c.strokeStyle=color;
            c.fillStyle='#fff';
            c.beginPath();
            c.arc(x,y,l,0,Math.PI*2);
            c.closePath();
            c.fill();
            c.stroke();
        },
        // 绘制颜色曲线提示
        drawTip(c,x,y,text,color){
            let width=36;
            let height=2;
            let margin=10;
            c.beginPath();
            c.fillStyle=color;
            c.rect(x-width/2,y-height/2,width,height);
            c.closePath();
            c.fill();
            this.drawCircle(c,x,y,8,'#999');
            this.drawText(c,x+margin+width/2,y-height/2,text,'#999');
        },
        // 绘制统计图底图 x,y为原点坐标
        drawChart(c,x,y,width,height,divide){
            let axisColor="#999";
            let lineColor="#dbdbdb";
            c.beginPath();
            c.strokeStyle=axisColor;
            c.moveTo(x,y);
            c.lineTo(x+width,y);
            c.closePath();
            c.stroke();
            c.beginPath();
            c.moveTo(x,y);
            c.lineTo(x,y-height);
            c.closePath();
            c.stroke();
            for(let i=0;i<=divide;i++){
                c.beginPath();
                c.strokeStyle="#ccc";
                c.moveTo(x,y-i*height/divide);
                c.lineTo(x+width,y-i*height/divide);
                c.closePath();
                c.stroke();
                this.drawCircle(c,x,y-i*height/divide);
                this.drawText(c,x-10,y-i*height/divide,i*this.axisLimit/divide,'#2c3242','right');
            }
            this.drawDataLine(c,x,y,this.lineData,0);
            this.drawDataLine(c,x,y,this.lineData,1);
            this.drawDataLine(c,x,y,this.lineData,2);
            for(let i=0;i<=this.axisNum;i++){
                this.drawCircle(c,x+i*width/(this.axisNum-1),y);
            }
            
        },
        // 绘制折线
        drawDataLine(c,x,y,data,index,width=1400,height=240){
            let numName=['studentNumber','teacherNumber','visitorNumber']
            console.log(data)
            c.beginPath();
            c.moveTo(x,y);
            c.strokeStyle=this.lineColor[index];
            data.forEach((d,i)=>{
                console.log('d: ', d);
                c.lineTo(x+i*width/(this.axisNum-1),y-height/this.axisLimit*d[numName[index]]);
            })
            c.stroke();
            data.forEach((d,i)=>{
                this.drawText(c,x+i*width/(this.axisNum-1),y+20,d.date,"#2c3242",'center','14px');
            })
            c.closePath();
            data.forEach((d,i)=>{
                this.drawCircle(c,x+i*width/(this.axisNum-1),y-height/this.axisLimit*d[numName[index]]);
            })
        },
        draw(){
            let canvas=this.$refs.canvas;
            let context =canvas.getContext('2d');
            context.clearRect(0,0,1500,this.topLength+30);
            let title="";
            if(this.chooseTitle=="流量分析"){
                title="流量统计";
            }else if(this.chooseTitle=="用户分析"){
                title="用户统计"
            }else if(this.chooseTitle=="后台首页"){
                title="访问统计"
            }
            this.drawText(context,10,10,title,"#2c3242");
            let perMargin=140;
            this.lineColor.forEach((color,index)=>{
                this.drawTip(context,1100+perMargin*index,20,this.lineTip[index],color);
            })
            this.drawChart(context,82,this.topLength,1400,240,5);
        }
    },
    mounted(){
        let requestType;
        if(this.chooseTitle=="流量分析"||this.chooseTitle=="后台首页"){
            requestType='traffic'
        }else if(this.chooseTitle=="用户分析"){
            requestType='user'
        }
        this.requsetAnalyseData({
            startTime:null,
            endTime:null,
            requestType,
            timeType:0,
            currentPage:0
        })
        let canvas=this.$refs.canvas;
        canvas.width=1500;
        canvas.height=this.topLength+30;
        this.axisNum=this.lineData.length;
        this.draw();
    },
    watch:{
        lineData:function(newVal,oldVal){
            this.axisNum=newVal.length;
            this.draw();
        }
    }
}
</script>

<style scoped>

</style>
