<template>
    <div class="login">
        <div class="loginContainer">
            <div class="main">
                <img src="../assets/img/loginLogo.png">
                <div class="formContainer">
                    <h1>蚂蚁置物</h1>
                    <div class="inputContainer">
                        <input type="text" placeholder="用户名" v-model="username" @keyup.enter="login">
                    </div>
                    <div class="inputContainer">
                        <input type="password" placeholder="密码" v-model="password" @keyup.enter="login">
                    </div>
                    <button @click="login">登录</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            username:'',
            password:''
        }
    },
    methods:{
        login(){
            console.log('login')
            let that=this;
            axios.post(`/background/administrator/login?account=${this.username}&passWord=${this.password}`
            ).then(res=>{
                if(res.data.msg==='登录成功'){
                    that.$router.push('/index')
                }else{
                    alert('登录失败')
                }
            }).catch(err=>{
                console.log(`some error happed called ${err} when login`)
            })
            console.log(111)
        }
    }
}
</script>

<style lang="less" scoped>
@import '../assets/less/define.less';
.login{
    height: 900px;
    padding-top: 160px;
    background-color: @topicShallowBColor;
    .loginContainer{
        width: 1200px;
        height: 600px;
        margin: 0 auto;
        border-radius:10px;
        background-color: #fff;
        .main{
            display: flex;
            padding: 100px;
            .formContainer{
                width: 400px;
                margin-left: 126px;
                text-align: center;
                h1{
                    margin-bottom: 65px;
                    font-size: 36px;
                    color:@topicShallowBColor;
                }
                .inputContainer{
                    height: 70px;
                    margin-bottom: 20px;
                    border-radius: 10px;
                    color:@topicShallowBColor;
                    background-color: #dfe3eb;
                    input{
                        border: none;
                        font-size: 24px;
                        line-height: 70px;
                        color: @topicShallowBColor;
                        background-color: transparent;
                        outline:none;
                    }
                }
                button{
                    border:none;
                    border-radius: 5px;
                    padding: 22px 116px;
                    font-size: 30px;
                    color: #fff;
                    background-color: @topicShallowBColor;
                    outline: none;
                    cursor: pointer;
                }
            }
        }
    }
}
</style>
