<template>
    <div class="whole">
        <Header :selectIndex="selectIndex"></Header>
        <workBench v-if="selectIndex==0" ></workBench>
        <contentManager v-if="selectIndex==1" ></contentManager>
        <accessCount v-if="selectIndex==2" ></accessCount>
    </div>
</template>

<script>
import Header from './construct/head.vue'
import workBench from './workBench.vue'
import contentManager from './contentManager.vue'
import accessCount from './accessCount.vue'
import store from '../store'
import {mapGetters} from 'vuex'
export default {
    data(){
        return{}
    },
    computed:{
        ...mapGetters('user',{
            selectIndex:"getSelectIndex"
        })
    },
    components:{
        Header,
        workBench,
        contentManager,
        accessCount
    },
    store
}
</script>

<style lang="less" scoped>
@import "../assets/less/define.less";
.whole{
    background-color: @borderColor;
}
</style>
