<template>
    <div class="accessCount">
        <leftNav @change-title="changeTitle" :navTitle="navTitle" :chooseTitle="chooseTitle"></leftNav>
            <Traffic v-show="chooseTitle=='流量分析'" :chooseTitle="chooseTitle"></Traffic>
            <User v-show="chooseTitle=='用户分析'" :chooseTitle="chooseTitle"></User>
            <Publish v-show="chooseTitle=='发布分析'" :chooseTitle="chooseTitle"></Publish>
            <Transaction v-show="chooseTitle=='交易分析'" :chooseTitle="chooseTitle"></Transaction>
    </div>
</template>

<script>
import leftNav from './construct/leftNav.vue'
import Traffic from './count/traffic.vue'
import User from './count/user.vue'
import Publish from './count/publish.vue'
import Transaction from './count/transaction.vue'

export default {
    data(){
        return{
            chooseTitle:"流量分析",
            navTitle:[{
                content:"流量分析",
                isExtension:false,
                child:""
            },{
                content:"用户分析",
                isExtension:false,
                child:""
            },{
                content:"发布分析",
                isExtension:false,
                child:""
            },{
                content:"交易分析",
                isExtension:false,
                child:""
            }]
        }
    },
    components:{
        leftNav,
        Traffic,
        User,
        Publish,
        Transaction
    },
    methods:{
        changeTitle(title,type){
            if(type=="parent"){
                if(!title.isExtension){
                    this.chooseTitle=title.content;
                }else{
                    // 展开或关闭
                }
            }else if(type=="child"){
                this.chooseTitle=title
            }
        }
    }
}
</script>

<style lang="less" scoped>
.accessCount{
    display: flex;
}
</style>
