const getters = {
    getTableData(state) {
        return state.tableData
    },
    getSectorData(state) {
        return state.sectorData
    },
    getLineData(state){
        return state.lineData
    },
    getManageData(state) {
        return state.manageData
    },
    getPage(state) {
        return state.page
    },
    getCurrentPage(state) {
        return state.currentPage
    }
}
const mutations = {
    // 修改表格数据
    setManageData(state, data) {
        state.page = data.allPage;
        let attr = Object.keys(data).filter(data => {
            return data != 'allPage'
        })[0]
        state.manageData = data[attr];
        state.manageData.forEach((val, index) => {
            val.serial = index + (state.currentPage - 1) * 8 + 1;
        })
    },
    // 修改分析数据
    setAnalyseData(state,data){
        state.tableData=data.analysisList;
        state.sectorData[0].num=data.booksNumber;
        state.sectorData[1].num=data.stationeryNumber;
        state.sectorData[2].num=data.dailyNumber;
        state.sectorData[3].num=data.cosmeticsNumber;
        state.sectorData[4].num=data.foodNumber;
        state.sectorData[5].num=data.electricalNumber;
        state.sectorData[6].num=data.othersNumber;
        console.log('state.sectorData: ', state.sectorData);
        state.page=data.allPage;
    },
    // 修改折线图数据
    setLineData(state,data){
        state.lineData=data.list;
    }
}
const actions = {
    //跳转页数
    turn({dispatch }, currentPage) {
        if (currentPage > state.page || currentPage < 1) {
            return;
        } else {
            let data={
                currentPage,
                startTime:window.sessionStorage.getItem("startTime"),
                endTime:window.sessionStorage.getItem("endTime"),
                requestType:window.sessionStorage.getItem("requestType"),
                timeType:window.sessionStorage.getItem("timeType"),
            }
            state.currentPage = currentPage;
            dispatch("requsetAnalyseData",data)
        }
    },
    // 请求数据
    requestManageData({ commit }, { tableType, currentPage, goodsType}) {
        let url = `/background/${tableType}/list?goodsType=${goodsType}&currentPage=${currentPage}`;
        axios.get(`${url}`)
            .then(res => {
                commit("setManageData", res.data)
            }).catch(err => {
                
            })
    },
    // 请求回收站的数据
    requestRecycleData({ commit }, { tableType, currentPage, goodsType}) {
        let url = `/background/${tableType}/listRecycle?goodsType=${goodsType}&currentPage=${currentPage}`;
        axios.get(`${url}`)
            .then(res => {
                commit("setManageData", res.data)
            }).catch(err => {
                
            })
    },
    // 删除数据
    deleteData({ commit , dispatch }, { tableType, id, type , currentPage, goodsType, dataType}) {
        let url = `/background/${tableType}/delete?type=${type}&idList=${id}`;
        axios.delete(`${url}`)
            .then(res => {
                if (res.data.msg === "删除成功"){
                    alert("删除成功");
                    if(dataType==='all')
                        dispatch("requestManageData",{tableType,currentPage,goodsType})
                    else if(dataType==='recycle')
                        dispatch("requestRecycleData",{tableType,currentPage,goodsType})
                }else {
                    alert("删除失败");
                }
            })
    },
    // 请求分析的数据
    requsetAnalyseData({commit},{startTime,endTime,requestType,timeType,currentPage}){
        let time=timeType;
        if(time===4){
            time=`${startTime}~${endTime}`;
        }
        window.sessionStorage.setItem("startTime",startTime);
        window.sessionStorage.setItem("endTime",endTime);
        window.sessionStorage.setItem("requestType",requestType);
        window.sessionStorage.setItem("timeType",timeType);
        let url=`/background/analysis/${requestType}Analysis?time=${time}&currentPage=${currentPage}`;
        axios.get(`${url}`)
            .then(res=>{
                if(requestType=='user'||requestType=='traffic'){
                    commit("setLineData",res.data);
                }
                commit("setAnalyseData",res.data);
            }).catch(err=>{
                
            })
    }

}

export default { state, getters, mutations, actions, namespaced }