<template>
    <div class="handle">
        <button :class="{'dateChoose':dateChoose==0,'timeBtn':true}" @click="getAnalyseData(0)">今日</button>
        <button v-if="chooseTitle=='发布分析'||chooseTitle=='交易分析'" :class="{'dateChoose':dateChoose==1,'timeBtn':true}" @click="getAnalyseData(1)">昨日</button>
        <button :class="{'dateChoose':dateChoose==2,'timeBtn':true}" @click="getAnalyseData(2)">最近7天</button>
        <button :class="{'dateChoose':dateChoose==3,'timeBtn':true}" @click="getAnalyseData(3)">最近30天</button>
        <span>时间：</span>
        <div class="time">
            <div>
                <span>{{startTime}}</span>
                <VueCtkDateTimePicker v-model="startTime" :noClearButton="true" :no-label="true" :onlyDate="true" :left="true" :minute-interval="30" :format="'YYYY-MM-DD'" color="#5d759d" button-color="rgb(246,146,138)"></VueCtkDateTimePicker>
            </div>
            ~
            <div>
                <span>{{endTime}}</span>
                <VueCtkDateTimePicker v-model="endTime" :noClearButton="true" :no-label="true" :onlyDate="true" :left="true" :minute-interval="30" format="YYYY-MM-DD" color="#5d759d" button-color="rgb(246,146,138)"></VueCtkDateTimePicker>
            </div>
        </div>
        <button class="search"  @click="getAnalyseData(4)">搜索</button>
    </div>
</template>

<script>
import VueCtk from 'vue-ctk-date-time-picker';
import {mapActions, mapGetters} from 'vuex'
export default {
    data(){
        return{
            startTime:'',
            endTime:'',
            dateChoose:0
        }
    },
    props:["chooseTitle"],
    components:{
        VueCtk,
    },
    computed:{
        ...mapGetters("analyse",{
            currentPage:'getCurrentPage'
        })
    },
    mounted(){
        this.startTime='2019-09-01';
        this.endTime='结束时间'
        this.getAnalyseData(0);
    },
    methods:{
        ...mapActions('analyse',["requsetAnalyseData"]),
        getAnalyseData(timeType){
            this.dateChoose=timeType;
            let startTime=this.startTime;
            let endTime=this.endTime;
            if(endTime==='结束时间'){
                let date=new Date();
                endTime=`${date.getYear()}-${date.getMonth()}-${date.getDate()}`;
            }
            let requestType="traffic";
            if(this.chooseTitle==='用户分析'){
                requestType="user"
            }else if(this.chooseTitle==='发布分析'){
                requestType="release"
            }else if(this.chooseTitle==='交易分析'){
                requestType="sell"
            }
            let currentPage=this.currentPage;
            let data={
                startTime,
                endTime,
                timeType,
                requestType,
                currentPage:currentPage
            }
            this.requsetAnalyseData(data)
        }
    },
    watch:{
        chooseTitle:function(newVal){
            this.getAnalyseData(0);
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../assets/less/define.less';
.handle{
    .timeBtn{
        padding: 11px 20px;
        margin-right: 20px;
        border: 1px solid #999;
        border-radius: 5px;
        font-size: 18px;
        color: #666;
        background-color: #f2f2f2;
        cursor: pointer;
        outline: none;
    }
    .dateChoose{
        border-color: @topicShallowBColor;
        color: #fff;
        background-color: @topicShallowBColor;
    }
    .time{
        display: inline-block;
        width: 300px;
        margin-right: 21px;
        border: 1px solid #999;
        >div{
            display: inline-block;
            position: relative;
            width: 138px;
            >span{
                display: inline-block;
                position: absolute;
                // left: 50%;
                width: 100%;
                top:50%;
                font-size: 18px;
                text-align: center;
                transform:  translateY(-50%);
            }
        }
    }
    .search{
        padding: 11px 21px;
        border-radius: 5px;
        border: 1px solid @topicShallowBColor;
        font-size: 18px;
        color: #fff;
        background-color: @topicShallowBColor;
        cursor: pointer;
    }
}


</style>

<style>

.field[data-v-7d90c7dc]{
    opacity: 0;
}
</style>
