<template>
    <div class="table">
        <div class="tableLabel">
            <span class="serial">序号</span>
            <span class="gType">物品类型</span>
            <span class="name">发布物品名称</span>
            <span class="pType">发布类型</span>
            <span class="money">发布金额</span>
        </div>
        <div v-for="data in tableData" :class="['tableData',data.serial%2==1?'singleData':'evenData']">
            <span class="serial">{{data.serial}}</span>
            <span class="gType">{{data.gType}}</span>
            <span class="name">{{data.name}}</span>
            <span class="pType">{{data.pType}}</span>
            <span class="money">{{data.money}}</span>
        </div>
        <div class="pageTurn">
            <span class="turnPage" @click="turn(currentPage-1)"><</span>
            <div class="pageContainer">
                <span :class="{'currentPage':p==currentPage}" v-for="p in page" v-cloak @click="turn(p)">{{p}}</span>
            </div>
            <span class="turnPage" 
                @click="turn(currentPage+1)">></span>
            <span v-cloak>共{{page}}页</span>
        </div>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
export default {
    computed:{
        ...mapGetters('analyse',{
            tableData:'getTableData',
            page:'getPage',
            currentPage:'getCurrentPage'
        })
    },
    methods:{
        ...mapActions("analyse",["turn"])
    }
}
</script>

<style lang="less" scoped>
@import '../../assets/less/define.less';
.table{
    display: inline-block;
    width: 1000px;
    height: 550px;
    margin-top: 40px;
    &>div{
        height: 50px;
        padding-left: 70px;
        margin-left: 19px;
        line-height: 50px;
        color: #666;
    }
    .tableLabel{
        border-bottom: 4px solid @topicDeepBColor;
        color: @topicDeepBColor;
        background-color: #e1e7fa;
    }
    .evenData{
        background-color: #e1e7fa;
    }
    .singleData{
        background-color: #eef1f5;
    }
    span{
        display: inline-block;
        overflow: hidden;
        text-align: center;
        white-space:nowrap;
        text-overflow:ellipsis;
    }
    .serial{
        width: 85px;
    }
    .gType{
        width: 150px;
    }
    .name{
        width: 310px;
    }
    .pType{
        width: 130px;
    }
    .money{
        width: 140px;
    }
    .pageTurn{
        @letterSpacing:9px;
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: 50px;
        letter-spacing: @letterSpacing;
        font-size: 24px;
        color: @shallowFontColor;
        transform: translatex(@letterSpacing);
        .turnPage{
            cursor: pointer;
        }
        .pageContainer{
            display: flex;
            flex-direction: row;
            min-width:50px; 
            justify-content: space-evenly;
            span{
                display: inline-block;
                width: 15px;
                padding: 0px 10px;
                text-align: center;
                cursor: pointer;
            }
            .currentPage{
                background-color: @topicShallowBColor;
                color: #fff;
            }
        }
    }
}
</style>
